import testBSC from './test/BSC'
import testMOON from './test/MOON'


export const ChainIdMap = {
    test_MOON: 1287,
    test_BSC: 97
}

const switchNet = (chainId)=>{
    if (chainId === ChainIdMap.test_BSC) {
        window.mainAddress = testBSC
    } else if (chainId === ChainIdMap.test_MOON) {
        window.mainAddress = testMOON
    }
}

export default switchNet