import { ethers } from "ethers";
import abi from "../abi/web3Abi/DaoFactory";
import switchNet from "../address/address"

let loadMain = false;
let maincontract;


export default async function mainConnect(state, dispatch) {
    const {web3InstanceState, maincontractState, web3Instance} = state;
    if (web3InstanceState !== 'READY' ) return;
    const asyncLoadMain = async () => {

        try {
            const network = await web3Instance.getNetwork()
            switchNet(network.chainId)
            const { DaoFactory } = window.mainAddress;

            let maincontract = new ethers.Contract(DaoFactory, abi, web3Instance);
            dispatch({type: 'SET_MAINCONTRACT', payload: maincontract});
            dispatch({type: 'SET_CHAINID', payload: network.chainId});
        } catch (e) {
            console.error(e);
            dispatch({type: 'MAINCONTRACT_ERROR'});
        }
    };
    if (maincontractState !== 'LOAD_MAINCONTRACT') return;
    if (loadMain) return dispatch({type: 'SET_MAINCONTRACT', payload: maincontract});
    loadMain = true;
    asyncLoadMain();
}
